<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
            </div>
            <el-row>
                <el-col class="line-right" :span="10">
            <el-form label-position="left" label-width="150px">
                <el-form-item label="Name Category">
                    {{ name }}
                </el-form-item>
                <el-form-item label="Icon">
                    <el-image
                        style="width: 100px; height: 100px"
                        :src="icon"
                        fit="cover">
                    </el-image>
                </el-form-item>
                <el-form-item label="Name Application">
                    {{  name_application  }}
                </el-form-item>
            </el-form>
            </el-col>
            <el-col :span="14">
                <el-row>
                <el-col :span="4" v-for="(o, index) in image" :key="index" style="margin : 5px" >
                    <el-card :body-style="{ padding: '0px' }">
                    <img :src="o.url" class="image">
                    <div style="margin: 0 auto;">
                        <el-button style="width : 100%" type="danger" icon="el-icon-delete" size="mini" @click="deleteData(o.id_images)">Delete</el-button>
                    </div>
                    </el-card>
                </el-col>
            </el-row>
            </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>

import axios from '../../../axios.js'
// import router from '@/router'

export default {
    data () {
        return {
            name : '',
            icon : '',
            name_application : '',
            image : []
        }
    },
    methods : {
        deleteData(dataID) {
          this.$alert('Are you sure you delete this image', 'Delete!', {
          confirmButtonText: 'OK',
          callback: action => {
            this.dataDel(dataID)
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
        },
        dataDel(dataID) {
            const accessToken = {
               headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
            }
            axios.dele(`image/${dataID}/${this.$route.params.id}/c/delete`, accessToken)
                .then((response) => {
                    if (response.data.success == true) {
                        const id = parseInt(response.data.data)
                        const ItemIndex = this.image.findIndex((c) => c.id_images === id)
                        this.image.splice(ItemIndex, 1)
                        // console.log('true')
                }
            })
        }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        },
        detailData () {
            return this.data
        }
        
    },
    async created () {
        const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }
        await axios.get(`categories/${this.$route.params.id}/detail`, accessToken)
            .then((response) => {    
            this.name = response.data.data.name_categories,
            this.icon = response.data.data.url,
            this.name_application = response.data.data.name_applications
            this.image = response.data.image
        })
    }
}
</script>

<style>
.form-application-add {
    margin-right: 25px;
    margin: 0 auto;
    width: 600px;
}
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
.btn-input-add {
    margin: 0 auto;
    width: 250px;
}
.time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }

</style>